<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/anchor-has-content -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <div class="card-body">
    <div
      class="modal fade"
      ref="staticBackdrop"
      data-bs-backdrop="static"
      data-bs-keyboard="false"
      tabindex="-1"
      aria-labelledby="staticBackdropLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-dialog-centered modal-xl">
        <div class="modal-content rounded-0 border-2 border-dark">
          <div
            class="modal-header paperDarkBg border-bottom border-dark border-2"
          >
            <h1 class="modal-title fs-5" id="staticBackdropLabel">
              ADD NEW PRODUCT
            </h1>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <VForm
            v-slot="{ meta }"
            @submit="AddEditProduct"
            class="d-flex flex-column h-100"
          >
            <div class="modal-body">
              <div class="row row-cols-1 row-cols-md-2 g-4">
                <!-- 圖片區 -->
                <div class="col">
                  <div class="card h-100 border-0">
                    <div class="card-body">
                      <div class="input-group mb-3">
                        <label
                          class="input-group-text w-100 p-4 d-flex flex-column align-items-center btn border-dark rounded"
                          for="inputGroupFile01"
                        >
                          <i class="bi bi-cloud-arrow-up h3"></i>
                          Drag an image or click here to upload…</label
                        >
                        <input
                          type="file"
                          ref="image_uploads"
                          @change="uploadImage"
                          name="image_uploads"
                          class="form-control d-none"
                          accept=".jpg, .jpeg, .png"
                          id="inputGroupFile01"
                        />
                      </div>
                      <div class="container">
                        <div class="row row-cols-1 row-cols-md-3 g-3">
                          <div
                            class="col position-relative border border-2 border-info rounded-3"
                            v-if="tempData.image"
                          >
                            <img :src="tempData.image" alt="" />
                            <a href="#" @click.prevent="deleteImage"
                              ><i
                                class="bi bi-x-lg text-danger position-absolute top-0 end-0"
                              ></i
                            ></a>
                          </div>
                          <template
                            v-for="(item, index) in tempData.images"
                            :key="index"
                          >
                            <div class="col position-relative">
                              <img :src="item" alt="" />
                              <a href="#" @click.prevent="deleteImages(index)">
                                <i
                                  class="bi bi-x-lg text-danger position-absolute top-0 end-0"
                                ></i>
                              </a>
                            </div>
                          </template>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <!-- 文字區 -->
                <div class="col">
                  <div class="card h-100 border-0">
                    <div class="card-body">
                      <h5 class="card-title">Product Discription</h5>
                      <div class="form-floating mb-3">
                        <VField
                          name="title"
                          type="text"
                          rules="required"
                          class="form-control border-dark"
                          id="floatingInput"
                          placeholder="title"
                          v-model="tempData.title"
                        />
                        <label for="floatingInput"
                          >Product Name
                          <ErrorMessage name="title" class="text-danger"
                        /></label>
                      </div>
                      <div class="form-floating mb-3">
                        <VField
                          name="comment"
                          as="textarea"
                          type="text"
                          rules="required"
                          class="form-control border-dark"
                          style="height: 100px"
                          id="floatingComment"
                          placeholder="Write your message here..."
                          v-model="tempData.comment"
                        />
                        <label for="floatingComment"
                          >Write your message here...
                          <ErrorMessage name="comment" class="text-danger"
                        /></label>
                      </div>
                      <!-- 選項 -->
                      <h5 class="card-title">Type</h5>
                      <div class="mb-3">
                        <template
                          v-for="(item, index) in categoryOption"
                          :key="index"
                        >
                          <input
                            type="radio"
                            class="btn-check"
                            name="options-type"
                            :id="`category${index}`"
                            autocomplete="off"
                            :value="item.value"
                            v-model="tempData.category"
                          />
                          <label
                            class="btn border-0 position-relative me-4"
                            :for="`category${index}`"
                          >
                            {{ item.title }}
                            <CircleTwo
                              :class="{
                                'd-none': tempData.category !== item.value,
                              }"
                            />
                          </label>
                        </template>
                      </div>
                      <h5 class="card-title mb-3">Option</h5>
                      <template
                        v-for="(data, key) in tempData.options"
                        :key="key"
                      >
                        <div class="input-group mb-3">
                          <span class="input-group-text border-dark">Size</span>
                          <VField
                            :name="`size${key}`"
                            type="text"
                            rules="required"
                            class="form-control border-dark"
                            v-model="tempData.options[key].size"
                          />
                          <span class="input-group-text border-dark">$</span>
                          <VField
                            :name="`price${key}`"
                            type="number"
                            rules="required|min_value:1"
                            class="form-control border-dark"
                            v-model="tempData.options[key].price"
                          />
                          <span class="input-group-text border-dark"
                            >Stock</span
                          >
                          <VField
                            :name="`stock${key}`"
                            type="number"
                            rules="required|min_value:1"
                            class="form-control border-dark"
                            id="floatingStock"
                            v-model="tempData.options[key].stock"
                          />
                          <span class="input-group-text border-dark"
                            ><a
                              class="m-0 p-0 text-danger text-decoration-none"
                              href="#"
                              @click.prevent="deleteOptionColumn(key)"
                            >
                              x
                            </a></span
                          >
                        </div>
                        <div class="mb-2">
                          <ErrorMessage
                            :name="`size${key}`"
                            class="text-danger"
                          />
                          <ErrorMessage
                            :name="`price${key}`"
                            class="text-danger"
                          />
                          <ErrorMessage
                            :name="`stock${key}`"
                            class="text-danger"
                          />
                        </div>
                      </template>
                      <div class="text-center">
                        <button
                          class="btn btn-outline-primary"
                          @click="addOptionColumn"
                        >
                          ADD NEW SPECIFICATION
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div
              class="modal-footer border-top border-dark border-2 justify-content-between paperDarkBg"
            >
              <button
                type="button"
                class="btn btn-outline-danger"
                data-bs-dismiss="modal"
                v-show="tempData._id"
                @click="deleteProduct(tempData._id)"
              >
                Delete
              </button>
              <div></div>
              <button
                class="btn btn-dark"
                :disabled="!meta.valid || !tempData.image"
                type="submit"
              >
                <span v-if="tempData._id">edit product</span>
                <span v-else>add product</span>
              </button>
            </div>
          </VForm>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.circle-2 {
  width: 5rem;
}
</style>

<script>
import CircleTwo from '@/components/helpers/focus/CircleTwo.vue';
import {
  apiAdminCreateProduct,
  apiAdminUpdateProduct,
  apiAdminDeleteProduct,
  apiUpLoad,
} from '@/assets/javascript/api';
import { Modal } from 'bootstrap';

export default {
  data() {
    return {
      staticBackdrop: '',
      tempData: {},
      categoryOption: [
        { title: 'Cake', value: 'cake' },
        { title: 'Coffee', value: 'coffee' },
        { title: 'Drink', value: 'drink' },
        { title: 'Bread', value: 'bread' },
      ],
    };
  },
  props: ['addEditData'],
  components: { CircleTwo },
  watch: {
    addEditData() {
      this.tempData = this.addEditData;
    },
  },
  methods: {
    AddEditProduct() {
      // eslint-disable-next-line no-underscore-dangle
      if (this.tempData._id) {
        // eslint-disable-next-line no-underscore-dangle
        apiAdminUpdateProduct(this.tempData._id, this.tempData)
          .then(() => {
            this.$emitter.emit('getProducts');
            this.toggleModal();
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        apiAdminCreateProduct(this.tempData)
          .then(() => {
            this.$emitter.emit('getProducts');
            this.toggleModal();
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    deleteProduct(id) {
      this.$emitter.emit('loadingStatus', false);
      apiAdminDeleteProduct(id)
        .then(() => {
          this.$emitter.emit('loadingStatus', true);
          this.$emitter.emit('getProducts');
        })
        .catch((err) => {
          console.log(err);
        });
    },
    toggleModal() {
      this.staticBackdrop.toggle();
    },
    // input
    addOptionColumn() {
      this.tempData.options.push({ size: '', price: 100, stock: 10 });
    },
    deleteOptionColumn(index) {
      if (this.tempData.options.length === 1) {
        return;
      }
      this.tempData.options.splice(index, 1);
    },
    getInputData() {
      this.$emitter.emit('inputData', this.tempData);
    },
    // img
    uploadImage() {
      this.$emitter.emit('loadingStatus', true);
      const formData = new FormData();
      formData.append(
        'file-to-upload',
        // eslint-disable-next-line comma-dangle
        this.$refs.image_uploads.form[0].files[0]
      );
      apiUpLoad(formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      })
        .then((res) => {
          this.$emitter.emit('loadingStatus', false);
          if (this.tempData.image) {
            this.tempData.images.push(res.data.data);
          } else {
            this.tempData.image = res.data.data;
          }
        })
        .catch((err) => {
          console.log(err);
          this.$emitter.emit('loadingStatus', false);
        });
    },
    deleteImage() {
      this.tempData.image = '';
      if (this.tempData.images.length > 0) {
        // eslint-disable-next-line prefer-destructuring
        this.tempData.image = this.tempData.images[0];
        this.tempData.images.splice(0, 1);
      }
    },
    deleteImages(i) {
      this.tempData.images.splice(i, 1);
    },
  },
  mounted() {
    this.staticBackdrop = new Modal(this.$refs.staticBackdrop);
  },
};
</script>
