<template>
  <section class="row overflow-scroll">
    <AddEditModal ref="addEdit" :addEditData="tempData" />
    <div class="card border-dark sticky-top paperDarkBg w-100">
      <div class="card-body d-flex align-items-start justify-content-between">
        <div class="h5 me-3 mt-auto d-flex align-items-center mb-0">
          <small class="text-secondary">2025/01/01</small>
          <i class="bi bi-caret-right-fill h6 mb-0 mx-2"></i>
          <small class="text-secondary">2025/01/08</small>,
          <small class="ms-3 text-secondary">weekly</small>
          <i class="bi bi-caret-down-fill h6 mb-0"></i>
        </div>
        <div>
          <button
            type="button"
            class="btn btn-primary"
            @click="openModal(true)"
          >
            cearte product
          </button>
        </div>
      </div>
    </div>
    <div class="card border-dark">
      <div class="card-body">
        <table class="table align-middle table-hover">
          <thead>
            <tr>
              <th scope="col" style="width: 30%">Product</th>
              <th scope="col" style="width: 10%">Type</th>
              <th scope="col" style="width: 10%">Size</th>
              <th scope="col" style="width: 10%">Price</th>
              <th scope="col" style="width: 10%">Inventory</th>
              <th scope="col" style="width: 15%">Status</th>
              <th scope="col" style="width: 10%">Edit</th>
            </tr>
          </thead>
          <tbody>
            <template v-for="item in productList" :key="item._id">
              <tr>
                <th>
                  <div class="d-flex align-items-center">
                    <img
                      :src="item.image"
                      style="width: 5rem"
                      alt=""
                      class="me-3"
                    />
                    {{ $t(item.title) }}
                  </div>
                </th>
                <td>{{ item.category }}</td>
                <td>
                  <ul class="list-unstyled mb-0">
                    <template
                      v-for="(data, index) in item.options"
                      :key="index"
                    >
                      <li class="border-bottom">{{ data.size }}</li>
                    </template>
                  </ul>
                </td>
                <td>
                  <ul class="list-unstyled mb-0">
                    <template
                      v-for="(data, index) in item.options"
                      :key="index"
                    >
                      <li class="border-bottom">${{ data.price }}</li>
                    </template>
                  </ul>
                </td>
                <td>
                  <ul class="list-unstyled mb-0">
                    <template
                      v-for="(data, index) in item.options"
                      :key="index"
                    >
                      <li class="border-bottom">{{ data.stock }}</li>
                    </template>
                  </ul>
                </td>
                <td>
                  <button
                    class="btn btn-secondary"
                    @click="toggleStatus(item)"
                    v-if="item.is_enable"
                  >
                    PUBLISHED
                  </button>
                  <button
                    class="btn btn-warning"
                    @click="toggleStatus(item)"
                    v-else
                  >
                    UNPUBLISHED
                  </button>
                </td>
                <td>
                  <button
                    class="btn btn-primary"
                    @click="openModal(false, item)"
                  >
                    EDIT
                  </button>
                </td>
              </tr>
            </template>
          </tbody>
        </table>
      </div>
    </div>
  </section>
</template>

<script>
import {
  apiAdminGetProducts,
  apiAdminUpdateProduct,
} from '@/assets/javascript/api';
import { getToken } from '@/assets/javascript/methods';
import AddEditModal from '@/components/dashboard/product/AddEditModel.vue';

export default {
  data() {
    return {
      staticBackdrop: '',
      productList: [],
      tempData: {},
    };
  },
  components: {
    AddEditModal,
  },
  methods: {
    toggleStatus(data) {
      this.$emitter.emit('loadingStatus', true);
      const tempData = { ...data };
      tempData.is_enable = !tempData.is_enable;
      // eslint-disable-next-line no-underscore-dangle
      apiAdminUpdateProduct(data._id, tempData)
        .then(() => {
          this.getProducts();
        })
        .catch((err) => {
          console.log(err);
          this.$emitter.emit('loadingStatus', false);
        });
    },
    getProducts() {
      this.$emitter.emit('loadingStatus', true);
      apiAdminGetProducts()
        .then((res) => {
          this.productList = res.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
      this.$emitter.emit('loadingStatus', false);
    },
    openModal(status, e) {
      if (status) {
        this.tempData = {
          _id: '',
          title: '',
          comment: '',
          category: 'cake',
          image: '',
          images: [],
          options: [{ size: '', price: 100, stock: 10 }],
        };
      } else {
        this.tempData = e;
      }
      this.$refs.addEdit.toggleModal();
    },
    checkSignIn() {
      // 取得 cookie 中的 token
      const token = getToken();
      // 若有 token 則登入，反之轉為登入頁面
      if (token) {
        this.$http.defaults.headers.common.Authorization = `Bearer ${token}`;
      } else {
        this.$router.push('/backend');
      }
    },
  },
  mounted() {
    this.getProducts();
    this.$emitter.on('getProducts', () => {
      this.getProducts();
    });
  },
  created() {
    this.checkSignIn();
  },
};
</script>
